import React, { Component } from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import ScrollAnimation from 'react-animate-on-scroll';

import Titulo from './Titulo';

import '../style/Experiencia.css';
import '../style/index.css';


class Experiencia extends Component {
    // constructor(){
    //     super();

    // }





    render() {
        return(
            <div className="Experiencia" id="Experiencia">
                <Titulo titleText="Experiencia" />
                <Container>
                    
                    <Row>
                        <Col>
                            <ScrollAnimation animateIn="slideInLeft">
                                <div className="Experiencia-card">
                                    <strong>Programadora frontend</strong> - Freelance
                                    <br />
                                    <span><em>Junio 2019 - Actualidad</em></span>
                                    <br />
                                    <span>Realización de varias páginas webs, como <a href="http://www.jsfisioterapia.com/" rel="noopener noreferrer" target="_blank">Js Fisioterapia</a> </span>                                
                                </div>
                            </ScrollAnimation>
                        </Col>
                    </Row>
                    <Row>
                        <Col>
                            <ScrollAnimation animateIn='flipInY' animateOut='flipOutY'>
                                <div className="Experiencia-card">
                                    <strong>Account Manager</strong> - Waavi Studios. S.L
                                    <br />
                                    <span><em>Noviembre 2018 - Actualidad</em></span>
                                    <br />
                                    <span>Por cambios en la empresa, cambio a Account Manager, donde trato con los clientes, entendiendo sus exigencias y ayudando a los programadores a comprenderlas. </span>  
                                    <br />
                                    <span>Realización de documentación funcional.</span>                              
                                </div>
                            </ScrollAnimation>
                        </Col>
                    </Row>
                    <Row>
                        <Col>
                            <ScrollAnimation animateIn='slideInRight'>
                                <div className="Experiencia-card">
                                    <strong>Programadora frontend junior</strong> - Waavi Studios. S.L
                                    <br />
                                    <span><em>Febrero 2018 - Noviembre 2018</em></span>
                                    <br />
                                    <span>Realización de páginas webs usando React.js y Vue.js, CSS con SASS y FlexBox, Git, Node.js y DialogFlow para crear bots conversacionales. </span>                                
                                </div>
                            </ScrollAnimation>
                        </Col>
                    </Row>
                </Container>
            </div>
        );
    }
}

export default Experiencia;